p: 0;\r\n\tright: 0;\r\n\tbottom: 0;\r\n\tleft: 0;\r\n\tbackground-size: cover;\r\n\tbackground-position: center;\r\n\ttransition: transform 0.2s;\r\n}\r\n  .blog-card .details,\r\n  .blog-card .details ul {\r\n\tmargin: auto;\r\n\tpadding: 0;\r\n\tlist-style: none;\r\n}\r\n  .blog-card .details {\r\n\tposition: absolute;\r\n\ttop: 0;\r\n\tbottom: 0;\r\n\tleft: -100%;\r\n\tmargin: auto;\r\n\ttransition: left 0.2s;\r\n\tbackground: rgba(0, 0, 0, 0.6);\r\n\tcolor: #fff;\r\n\tpadding: 10px;\r\n\twidth: 100%;\r\n\tfont-size: 0.9rem;\r\n}\r\n  .blog-card .details a {\r\n\t-webkit-text-decoration: dotted underline;\r\n\t\t\ttext-decoration: dotted underline;\r\n}\r\n  .blog-card .details ul li {\r\n\tdisplay: inline-block;\r\n}\r\n  .blog-card .details .author:before {\r\n\tfont-family: FontAwesome;\r\n\tmargin-right: 10px;\r\n\tcontent: \"\";\r\n}\r\n  .blog-card .details .date:before {\r\n\tfont-family: FontAwesome;\r\n\tmargin-right: 10px;\r\n\tcontent: \"\";\r\n}\r\n  .blog-card .details .tags ul:before {\r\n\tfont-family: FontAwesome;\r\n\tcontent: \"\";\r\n\tmargin-right: 10px;\r\n}\r\n  .blog-card .details .tags li {\r\n\tmargin-right: 2px;\r\n}\r\n  .blog-card .details .tags li:first-child {\r\n\tmargin-left: -4px;\r\n}\r\n  .blog-card .description {\r\n\tpadding: 1rem;\r\n\tbackground: #fff;\r\n\tposition: relative;\r\n\tz-index: 1;\r\n  -webkit-line-clamp: 5;\r\n  height: 300px;\r\n  font-size: 0.85rem;\r\n  text-align: justify;\r\n}\r\n  .blog-card .description h1 {\r\n\tline-height: 1;\r\n\tmargin: 0;\r\n\tfont-size: 1.5rem;\r\n  font-family: 'Franklin Gothic Medium';\r\n}\r\n.blog-card .description .detail {\r\n\tfont-size: 1.1rem;\r\n  -webkit-line-clamp: 5;\r\n}\r\n  .blog-card .description .read-more {\r\n\ttext-align: left;\r\n}\r\n  .blog-card .description .read-more a {\r\n\tcolor: #064ea2;\r\n\tdisplay: inline-block;\r\n\tposition: relative;\r\n}\r\n  .blog-card .description .read-more a:after {\r\n\tcontent: \"\";\r\n\tfont-family: FontAwesome;\r\n\tmargin-left: -10px;\r\n\topacity: 0;\r\n\tvertical-align: middle;\r\n\ttransition: margin 0.3s, opacity 0.3s;\r\n}\r\n  .blog-card .description .read-more a:hover:after {\r\n\tmargin-left: 5px;\r\n\topacity: 1;\r\n}\r\n  .blog-card p {\r\n\tposition: relative;\r\n\tmargin: 1rem 0 0;\r\n}\r\n  .blog-card p:first-of-type {\r\n\tmargin-top: 1.25rem;\r\n}\r\n  .blog-card p:first-of-type:before {\r\n\tcontent: \"\";\r\n\tposition: absolute;\r\n\theight: 5px;\r\n\tbackground: #064ea2;\r\n\twidth: 35px;\r\n\ttop: -0.75rem;\r\n\tborder-radius: 3px;\r\n}\r\n  .blog-card:hover .details {\r\n\tleft: 0%;\r\n}\r\n@media (min-width: 640px) {\r\n\t.blog-card {\r\n\t  flex-direction: row;\r\n\t  max-width: 800px;\r\n\t}\r\n\t.blog-card .meta {\r\n\t  flex-basis: 40%;\r\n\t  height: auto;\r\n\t}\r\n\t.blog-card .description {\r\n\t  flex-basis: 60%;\r\n\t}\r\n\t.blog-card .description:before {\r\n\t  transform: skewX(-3deg);\r\n\t  content: \"\";\r\n\t  background: #fff;\r\n\t  width: 30px;\r\n\t  position: absolute;\r\n\t  left: -10px;\r\n\t  top: 0;\r\n\t  bottom: 0;\r\n\t  z-index: -1;\r\n\t}\r\n\t/* .blog-card.alt {\r\n\t  flex-direction: row-reverse;\r\n\t}\r\n\t.blog-card.alt .description:before {\r\n\t  left: inherit;\r\n\t  right: -10px;\r\n\t  transform: skew(3deg);\r\n\t}\r\n\t.blog-card.alt .details {\r\n\t  padding-left: 25px;\r\n\t} */\r\n}\r\n\r\n/* Post Content */\r\n.post-header{\r\n    width: 100%;\r\n    height: 500px;\r\n    background: var(--container-color);\r\n}\r\n.post-container{\r\n    max-width: 800px;\r\n    margin: auto;\r\n    width: 100%;\r\n}\r\n.header-content{\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    margin-top: 4rem !important;\r\n}\r\n.back-home {\r\n    color: var(--second-color);\r\n    font-size: 0.9rem;\r\n}\r\n.header-title{\r\n    width: 90%;\r\n    font-size: 2.6rem;\r\n    color: var(--bg-color);\r\n    text-align: center;\r\n    margin-bottom: 1rem;\r\n}\r\n.header-img{\r\n    width: 100%;\