if;\r\n    position: relative;\r\n    overflow: hidden;\r\n    margin: 10px;\r\n    min-width: 220px;\r\n    max-width: 310px;\r\n    width: 100%;\r\n    background: #34495e;\r\n    color: #333;\r\n    text-align: center;\r\n    box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);\r\n    font-size: 16px;\r\n}\r\n\r\nfigure.member * {\r\n    -webkit-box-sizing: border-box;\r\n    box-sizing: border-box;\r\n    -webkit-transition: all 0.35s ease;\r\n    transition: all 0.35s ease;\r\n}\r\n\r\nfigure.member img {\r\n    max-width: 100%;\r\n    vertical-align: top;\r\n}\r\n\r\nfigure.member .image {\r\n    position: relative;\r\n    border-bottom: 4px solid #34495e;\r\n    z-index: 1;\r\n}\r\n\r\nfigure.member .image:before {\r\n    width: 0;\r\n    height: 0;\r\n    border-style: solid;\r\n    border-width: 20px 20px 0 20px;\r\n    border-color: #34495e transparent transparent transparent;\r\n    content: '';\r\n    position: absolute;\r\n    top: 100%;\r\n    left: 50%;\r\n    -webkit-transform: translate(-50%, -15px);\r\n    transform: translate(-50%, -15px);\r\n    z-index: -1;\r\n    -webkit-transition: all 0.35s ease;\r\n    transition: all 0.35s ease;\r\n}\r\n\r\nfigure.member .image p {\r\n    font-weight: 500;\r\n    margin: 0;\r\n    padding: 0 30px;\r\n    line-height: 1.6em;\r\n    position: absolute;\r\n    top: 50%;\r\n    width: 100%;\r\n    color: #ffffff;\r\n    -webkit-transform: translateY(-50%);\r\n    transform: translateY(-50%);\r\n    opacity: 0;\r\n    font-style: italic;\r\n}\r\n\r\nfigure.member figcaption {\r\n    background-color: #ffffff;\r\n    padding: 25px;\r\n}\r\n\r\nfigure.member h3 {\r\n    margin: 0 0 5px;\r\n    text-transform: uppercase;\r\n    font-weight: 400;\r\n}\r\n\r\nfigure.member h4 span {\r\n    font-weight: 800;\r\n}\r\n\r\nfigure.member h5 {\r\n    margin: 0 0 15px;\r\n    font-weight: 400;\r\n}\r\n\r\nfigure.member i {\r\n    margin-right: 5px;\r\n    display: inline-block;\r\n    font-size: 24px;\r\n    color: #000000;\r\n    widt