 0.3s ease;\r\n}\r\n\r\n/*--------------------------------------------------------------\r\n# Blog\r\n--------------------------------------------------------------*/\r\n.blog-card {\r\n\tdisplay: flex;\r\n\tflex-direction: column;\r\n\tmargin: 1rem auto;\r\n\tbox-shadow: 0 3px 7px -1px rgba(0, 0, 0, 0.1);\r\n\tmargin-bottom: 1.6%;\r\n\tbackground: #AEBDCA;\r\n\tline-height: 1.4;\r\n\tfont-family: sans-serif;\r\n\tborder-radius: 5px;\r\n\toverflow: hidden;\r\n\tz-index: 0;\r\n}\r\n  .blog-card a {\r\n\tcolor: inherit;\r\n}\r\n  .blog-card a:hover {\r\n\tcolor: #064ea2;\r\n}\r\n  .blog-card:hover .photo {\r\n\ttransform: scale(1.3) rotate(3deg);\r\n}\r\n  .blog-card .meta {\r\n\tposition: relative;\r\n\tz-index: 0;\r\n\theight: 200px;\r\n}\r\n  .blog-card .photo {\r\n\tposition: absolute;\r\n\ttop: 0;\r\n\tright: 0;\r\n\tbottom: 0;\r\n\tleft: 0;\r\n\tbackground-size: cover;\r\n\tbackground-position: center;\r\n\ttransition: transform 0.2s;\r\n}\r\n  .blog-card .details,\r\n  .blog-card .details ul {\r\n\tmargin: auto;\r\n\tpadding: 0;\r\n\tlist-style: none;\r\n}\r\n  .blog-card .details {\r\n\tposition: absolute;\r\n\ttop: 0;\r\n\tbottom: 0;\r\n\tleft: -100%;\r\n\tmargin: auto;\r\n\ttransition: left 0.2s;\r\n\tbackground: rgba(0, 0, 0, 0.6);\r\n\tcolor: #fff;\r\n\tpadding: 10px;\r\n\twidth: 100%;\r\n\tfont-size: 0.9rem;\r\n}\r\n  .blog-card .details a {\r\n\t-webkit-text-decoration: dotted underline;\r\n\t\t\ttext-decoration: dotted underline;\r\n}\r\n  .blog-card .details ul li {\r\n\tdisplay: inline-block;\r\n}\r\n  .blog-card .details .author:before {\r\n\tfont-family: FontAwesome;\r\n\tmargin-right: 10px;\r\n\tcontent: \"\";\r\n}\r\n  .blog-card .details .date:before {\r\n\tfont-family: FontAwesome;\r\n\tmargin-right: 10px;\r\n\tcontent: \"\";\r\n}\r\n  .blog-card .details .tags ul:before {\r\n\tfont-family: FontAwesome;\r\n\tcontent: \"\";\r\n\tmargin-right: 10px;\r\n}\r\n  .blog-card .details .tags li {\r\n\tmargin-right: 2px;\r\n}\r\n  .blog-card .details .tags li:first-child {\r\n\tmargin-left: -4px;\r\n}\r\n  .blog-card .description {\r\n\tpadding: 1rem;\r\n\tbackground: #fff;\r\n\tposition: relative;\r\n\tz-index: 1;\r\n  -webkit-line-clamp: 5;\r\n  height: 300px;\r\n  font-size: 0.85rem;\r\n  text-align: justify;\r\n}\r\n  .blog-card .description h1 {\r\n\tline-height: 1;\r\n\tmargin: 0;\r\n\tfont-size: 1.5rem;\r\n  font-family: 'Franklin Gothic Medium';\r\n}\r\n.blog-card .description .detail {\r\n\tfont-size: 1.1rem;\r\n  -webkit-line-clamp: 5;\r\n}\r\n  .blog-card .description .read-more {\r\n\ttext-align: left;\r\n}\r\n  .blog-card .description .read-more a {\r\n\tcolor: #064ea2;\r\n\tdisplay: inline-block;\r\n\tposition: relative;\r\n}\r\n  .blog-card .description .read-more a:after {\r\n\tcontent: \"\";\r\n\tfont-family: FontAwesome;\r\n\tmargin-left: -10px;\r\n\topacity: 0;\r\n\tvertical-align: middle;\r\n\ttransition: margin 0.3s, opacity 0.3s;\r\n}\r\n  .blog-card .description .read-more a:hover:after {\r\n\tmargin-left: 5px;\r\n\topacity: 1;\r\n}\r\n  .blog-card p {\r\n\tposition: relative;\r\n\tmargin: 1rem 0 0;\r\n}\r\n  .blog-card p:first-of-type {\r\n\tmargin-top: 1.25rem;\r\n}\r\n  .blog-card p:first-of-type:before {\r\n\tcontent: \"\";\r\n\tposition: absolute;\r\n\theight: 5px;\r\n\tbackground: #064ea2;\r\n\twidth: 35px;\r\n\ttop: -0.75rem;\r\n\tborder-radius: 3px;\r\n}\r\n  .blog-card:hover .details {\r\n\tleft: 0%;\r\n}\r\n@media (min-width: 640px) {\r\n\t.blog-card {\r\n\t  flex-direction: row;\r\n\t  max-width: 800px;\r\n\t}\r\n\t.blog-card .meta {\r\n\t  flex-basis: 40%;\r\n\t  height: auto;\r\n\t}\r\n\t.blog-card .description {\r\n\t  flex-basis: 60%;\r\n\t}\r\n\t.blog-card .description:before {\r\n\t  transform: skewX(-3deg);\r\n\t  content: \"\";\r\n\t  background: #fff;\r\n\t  width: 30px;\r\n\t  position: absolute;\r\n\t  left: -10px;\r\n\t  top: 0;\r\n\t  bottom: 0;\r\n\t  z-index: -1;\r\n\t}\r\n\t/* .blog-card.alt {\r\n\t  flex-direction: row-reverse;\r\n\t}\r\n\t.blog-card.alt .description:before {\r\n\t  left: inherit;\r\n\t  right: -10px;\r\n\t  transform: skew(3deg);\r\n\t}\r\n\t.blog-card.alt .details {\r\n\t  padding-left: 25px;\r\n\t} */\r\n}\r\n\r\n/* Post Content */\r\n.post-header{\r\n    width: 100%;\r\n    height: 500px;\r\n    background: var(--container-color);\r\n}\r\n.post-container{\r\n    max-width: 800px;\r\n    margin: auto;\r\n    width: 100%;\r\n}\r\n.header-content{\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    margin-top: 4rem !important;\r\n}\r\n.back-home {\r\n    color: var(--second-color);\r\n    font-size: 0.9rem;\r\n}\r\n.header-title{\r\n    width: 90%;\r\n    font-size: 2.6rem;\r\n    color: var(--bg-color);\r\n    text-align: center;\r\n    margin-bottom: 1rem;\r\n}\r\n.header-img{\r\n    width: 100%;\r\n    height: 420px;\r\n    object-fit: cover;\r\n    object-position: center;\r\n}\r\n.post-content{\r\n    margin-top: 10rem !important;\r\n}\r\n.sub-heading{\r\n    font-size: 1.2rem;\r\n}\r\n.post-text{\r\n    font-size: 1rem;\r\n    line-height: 1.7rem;\r\n    margin: 1rem 0;\r\n    text-align: justify;\r\n}\r\n\r\n/*--------------------------------------------------------------\r\n# Blog Detail\r\n--------------------------------------------------------------*/\r\n.blog {\r\n  margin: 0 auto;\r\n  width: 100%;\r\n  max-width: 800px;\r\n  display: grid;\r\n  grid-template-columns: 1fr;\r\n  gap: 18px 16px;\r\n  text-align: justify;\r\n}\r\n\r\n.blog .blog__poster {\r\n  width: 100%;\r\n  max-width: 400px;\r\n  border-radius: 10px;\r\n  float: left;\r\n  margin-right: 25px;\r\n  /* display: block;\r\n  margin-left: auto;\r\n  margin-right: auto;\r\n  width: 50%; */\r\n}\r\n\r\n.blog .blog__info h4 {\r\n  margin: 8px 0;\r\n}\r\n\r\n/*--------------------------------------------------------------\r\n# Share\r\n--------------------------------------------------------------*/\r\n.share{\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    row-gap: 1rem;\r\n}\r\n.share-title{\r\n    font-size: 1.1rem;\r\n}\r\n\r\n/*--------------------------------------------------------------\r\n# Footer\r\n--------------------------------------------------------------*/\r\n.footer {\r\n    padding: 20px 0;\r\n    font-size: 14px;\r\n    transition: all 0.3s;\r\n    border-top: 1px solid #cddfff;\r\n  }\r\n  \r\n  .footer .copyright {\r\n    text-align: center;\r\n    color: #012970;\r\n  }\r\n  \r\n  .footer .credits {\r\n    padding-top: 5px;\r\n    text-align: center;\r\n    font-size: 13px;\r\n    color: #012970;\r\n  }\r\n\r\n/*--------------------------------------------------------------\r\n  # About Us\r\n  --------------------------------------------------------------*/\r\n\r\nfigure.member {\r\n    font-family: 'Raleway', Arial, sans-serif;\r\n    position: relative;\r\n    overflow: hidden;\r\n    margin: 10px;\r\n    min-width: 220px;\r\n    max-width: 310px;\r\n    width: 100%;\r\n    background: #34495e;\r\n    color: #333;\r\n    text-align: center;\r\n    box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);\r\n    font-size: 16px;\r\n}\r\n\r\nfigure.member * {\r\n    -webkit-box-sizing: border-box;\r\n    box-sizing: border-box;\r\n    -webkit-transition: all 0.35s ease;\r\n    transition: all 0.35s ease;\r\n}\r\n\r\nfigure.member img {\r\n    max-width: 100%;\r\n    vertical-align: top;\r\n}\r\n\r\nfigure.member .image {\r\n    position: relative;\r\n    border-bottom: 4px solid #34495e;\r\n    z-index: 1;\r\n}\r\n\r\nfigure.member .image:before {\r\n    width: 0;\r\n    height: 0;\r\n    border-style: solid;\r\n    border-width: 20px 20px 0 20px;\r\n    border-color: #34495e transparent transparent transparent;\r\n    content: '';\r\n    position: absolute;\r\n    top: 100%;\r\n    left: 50%;\r\n    -webkit-transform: translate(-50%, -15px);\r\n    transform: translate(-50%, -15px);\r\n    z-index: -1;\r\n    -webkit-transition: all 0.35s ease;\r\n    transition: all 0.35s ease;\r\n}\r\n\r\nfigure.member .image p {\r\n    font-weight: 500;\r\n    margin: 0;\r\n    padding: 0 30px;\r\n    line-height: 1.6em;\r\n    position: absolute;\r\n    top: 50%;\r\n    width: 100%;\r\n    color: #ffffff;\r\n    -webkit-transform: translateY(-50%);\r\n    transform: translateY(-50%);\r\n    opacity: 0;\r\n    font-style: italic;\r\n}\r\n\r\nfigure.member figcaption {\r\n    background-color: #ffffff;\r\n    padding: 25px;\r\n}\r\n\r\nfigure.member h3 {\r\n    margin: 0 0 5px;\r\n    text-transform: uppercase;\r\n    font-weight: 400;\r\n}\r\n\r\nfigure.member h4 span {\r\n    font-weight: 800;\r\n}\r\n\r\nfigure.member h5 {\r\n    margin: 0 0 15px;\r\n    font-weight: 400;\r\n}\r\n\r\nfigure.member i {\r\n    margin-right: 5px;\r\n    display: inline-block;\r\n    font-size: 24px;\r\n    color: #000000;\r\n    width: 35px;\r\n    height: 35px;\r\n    line-height: 35px;\r\n    background: white;\r\n    box-shadow: 0 0 3px rgba(0, 0, 0, 0.4);\r\n    background-color: rgba(0, 0, 0, 0.05);\r\n}\r\n\r\nfigure.member i:hover {\r\n    background-color: #34495e;\r\n    color: white !important;\r\n}\r\n\r\nfigure.member:hover .image:before,\r\nfigure.member.hover .image:before {\r\n    border-color: #34495e transparent transparent transparent;\r\n    -webkit-transform: translate(-50%, 0px);\r\n    transform: translate(-50%, 0px);\r\n}\r\n\r\nfigure.member:hover .image p,\r\nfigure.member.hover .image p {\r\n    opacity: 1;\r\n}\r\n\r\nfigure.member:hover img,\r\nfigure.member.hover img {\r\n    opacity: 0.2;\r\n    -webkit-filter: grayscale(100%);\r\n    filter: grayscale(100%);\r\n}"],"sourceRoot":""}]);
