// import homeDashboardFunctions from "../../../scripts/utils/homeDashboardFunctions";
import dataPosts from "../../../scripts/utils/dataBlogs";
import { formatDate } from "../../../scripts/utils/functions";
import dashboardHomescreenFunctions from "../../../scripts/utils/dashboardHomescreenFunctions";


const dashboardHome = {
  async render() {
    return `
      

      <section class="hero-section hero-section-full-height">
      <div class="container-fluid">
          <div class="row">

              <div class="col-lg-12 col-12 p-0">
                  <div id="hero-slide" class="carousel carousel-fade slide" data-bs-ride="carousel">
                      <div class="carousel-inner">
                          <div class="carousel-item active">
                              <img src="../assets/img/slide/banner.jpg" class="carousel-image img-fluid" alt="...">
                                
                              <div class="carousel-caption d-flex flex-column justify-content-end">
                                  <h2>KEMENPORA </h2>
                                  <span>Republik Indonesia</span>
                                    
                                  <p>DIVISI SISINFO</p>
                              </div>
                          </div>

                          <div class="carousel-item">
                              <img src="../assets/img/slide/banner.jpg" class="carousel-image img-fluid" alt="..."> 
                              
                              <div class="carousel-caption d-flex flex-column justify-content-end">
                                  <h2>SISTEM INFORMASI</h2>
                                  
                                  <p>Sistem Informasi</p>
                              </div>
                          </div>
                      </div>

                      <button class="carousel-control-prev" type="button" data-bs-target="#hero-slide" data-bs-slide="prev">
                          <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                          <span class="visually-hidden">Previous</span>
                      </button>

                      <button class="carousel-control-next" type="button" data-bs-target="#hero-slide" data-bs-slide="next">
                          <span class="carousel-control-next-icon" aria-hidden="true"></span>
                          <span class="visually-hidden">Next</span>
                      </button>
                  </div>
              </div>

          </div>
      </div>
    </section>


    <br></br>
    <section class="section-padding section-bg" id="section_2">
        <div class="container" data-aos="fade-up">

        
          <div class="col-lg-12 col-12 text-center mx-auto" styl>
            <h2 class="mb-5">Layanan Kemenpora </h2>
          </div>


            <div class="row wide-row" id='daftarMenu'>

            </div>
        </div>
        
    </section>

    <section class="section-padding section-bg" >
      <div class ="card" style ="max-width: 60%; margin:auto; border-radius:15px;">
      <div class="container" data-aos="fade-up">
          <div class="col-lg-12 col-12 text-center mx-auto">
            <h2 class="mb-5">Data </h2>   
          </div>
          <div class="row" id="postsList">
      </div>
      </div>
    </section>

    <br></br>
    <section id="values" class="values" style="background: #f6f9ff;">
      <div class="container" data-aos="fade-up">
      <div class="col-lg-12 col-12 text-center mx-auto" styl>
      <h2 class="mb-5">Visi Kemenpora </h2>
      </div>

        <div class="row">
          <div class="col-lg-4" data-aos="fade-up" data-aos-delay="200" style="padding: 30px;">
            <div class="box" style="background: #ffff;">
              
              <h3>Pemuda berkualitas</h3>
              <p>Pemuda yang beriman dan bertakwa kepada Tuhan Yang Maha Esa, berakhlak mulia, sehat, cerdas, kreatif, inovatif, mandiri, demokratis, bertanggungjawab, berdaya saing, serta memiliki jiwa kepemimpinan, kewirausahaan, kepeloporan, dan kebangsaan sesuai dengan nilai-nilai Pancasila.</p>
            </div>
          </div>

          <div class="col-lg-4 mt-4 mt-lg-0" data-aos="fade-up" data-aos-delay="400" style="padding: 30px;">
            <div class="box" style="background: #ffff;">
              
              <h3>Masyarakat berbudaya olahraga</h3>
              <p>Masyarakat yang aktivitas kesehariaannya menjadikan olahraga sebagai gaya hidup guna meningkatkan kebugaran dan menjaga kesehatan dalam berkontribusi pada pembangunan sumber daya manusia berkualitas dan berdaya saing.

              </p>
            </div>
          </div>

          <div class="col-lg-4 mt-4 mt-lg-0" data-aos="fade-up" data-aos-delay="600" style="padding: 30px;">
            <div class="box" style="background: #ffff;">
              
              <h3>Bangsa berprestasi olahraga di tingkat internasional</h3>
              <p>Indonesia berprestasi di multievent dan single event olahraga tingkat regional Asia dan dunia, terutama pada cabang olahraga Olimpiade baik pada event elite junior maupun elite senior.</p>
            </div>
          </div>

    
        </div>

      </div>

    </section>


    <section id="values" class="values" style="background: #f6f9ff;">
      <div class="container" data-aos="fade-up">
      <div class="col-lg-12 col-12 text-center mx-auto" styl>
      <h2 class="mb-5">Misi Kemenpora </h2>
      </div>

        <div class="row">
          <div class="col-lg-4" data-aos="fade-up" data-aos-delay="200" style="padding: 30px;">
            <div class="box" style="background: #ffff;">

              <p>Merumuskan dan menetapkan kebijakan pembangunan kepemudaan dan keolahragaan dalam rangka mewujudkan pemuda berkualitas, masyarakat berbudaya olahraga dan peningkatan prestasi olahraga di tingkat internasional.</p>
              <p>Mengkoordinasikan dan mensinkronkan pelaksanaan kebijakan untuk mewujudkan pemuda berkualitas, masyarakat berbudaya olahraga dan peningkatan prestasi olahraga di tingkat internasional.</p>
            </div>
          </div>

          <div class="col-lg-4 mt-4 mt-lg-0" data-aos="fade-up" data-aos-delay="400" style="padding: 30px;">
            <div class="box" style="background: #ffff;">
              
  
              <p>Mengawasi penyelenggaraan pembangunan bidang kepemudaan dan keolahragaan guna mendukung pencapaian pembangunan sumber daya manusia berkualitas dan berdaya saing.</p>
              <p>Membangun kemitraan, jejaring kerja, peran serta dan kerja sama bidang kepemudaan dan keolahragaan di tingkat internasional.</p>
       
            </div>
          </div>

          <div class="col-lg-4 mt-4 mt-lg-0" data-aos="fade-up" data-aos-delay="600" style="padding: 30px;">
            <div class="box" style="background: #ffff;">
              <p>Meningkatkan inovasi dan peningkatan penggunaan teknologi sebagai basis dalam pembangunan kepemudaan dan keolahragaan.</p>
              <p>Meningkatkan tata kelola kelembagaan, kompetensi ASN, penyederhanaan regulasi, penyesuaian birokrasi, dan peningkatan kecepatan pelayanan publik.</p>

            </div>
          </div>

    
        </div>

      </div>

    </section>
    


    

<br></br>



`;
  },

  async afterRender() {
    console.log("kode jalan");
    const daftarMenuContainer = document.getElementById("daftarMenu");
    const fetchedMenu = await dashboardHomescreenFunctions.getAllMenu();
    let countTitle = 0; // Menambahkan counter untuk limit item yang ditampilkan

    fetchedMenu.forEach((data) => {
      if (countTitle < 6) {
        // Menambahkan kondisi untuk hanya menampilkan 5 item
        let nama_menu = data.data().nama_menu;
        if (nama_menu.length > 8) {
          // Memotong teks jika lebih dari 10 karakter
          nama_menu = nama_menu.substring(0, 8) + "...";
        }

        daftarMenuContainer.innerHTML += `
                    <div class="col-lg-2 col-md-6 col-12 mb-4 mb-lg-0 mb-md-4">
                        <div class="featured-block d-flex justify-content-center align-items-center">
                            <a href="${
                              data.data().link
                            }" target="_blank" class="d-block">
                                <img src="${
                                  data.data().gambar
                                }" class="featured-block-image img-fluid" alt="" style="object-fit:cover;width:150px;height:150px; border-radius:10px">
                                <p class="featured-block-text " style="font-size: 18px;"><strong>${nama_menu}</strong></p>
                            </a>
                        </div>
                    </div>`;

        countTitle++; // Meningkatkan counter setiap kali item ditampilkan
      }
    });

    if (countTitle === 6) {
      // Menambahkan tombol jika sudah menampilkan 5 item
      daftarMenuContainer.innerHTML += `
                <div class="col-lg-12 col-md-12 col-12 text-center" style="margin:12px;">
                    <a href="/all-menu" target="_blank" class="btn btn-primary">Lihat Layanan</a>
                </div>`;
    }

    // start Carousel Menu Layanan Kemenpora

    const fetchedBlog = await dataPosts._fetchAllDataPosts();
    const postsList = document.getElementById("postsList");

    let count = 0;
    fetchedBlog.forEach((d) => {
      if (count < 2) {
        const data = d.data();
        data.id = d.id;

        postsList.innerHTML += `
                <div class="col-md-6">
                    <div class="blog-card" style="background-color:#AEBDCA; ">
                        <div class="meta">
                            <div class="photo" style="background-image: url(${
                              data.foto
                            }); border-radius : 10px;"></div>
                            <ul class="details">
                                <li class="author">${data.kategori}</li>
                                <li class="date">${formatDate(
                                  data.tgl_dibuat
                                )}</li>
                            </ul>
                        </div>
                        <div class="description">
                            <h1>${data.judul}</h1>
                            <p class="read-more">
                                <a href="#/detailBlog/${data.id}">Read More</a>
                            </p>
                        </div>
                    </div>
                </div>`;

        count++;
      }
    });

    if (count === 2) {
      // Menambahkan tombol jika sudah menampilkan 5 item
      postsList.innerHTML += `
                <div class="col-lg-12 col-md-12 col-12 text-center" style="margin:12px;">
                    <a href="#/blog"  class="btn btn-primary">Data Lainnya</a>
                </div>`;
    }


  },
};

export default dashboardHome;
